import styled from "styled-components";
import { useContext, useState, useCallback, useMemo } from "react";
import { FixedCts } from "../../index";
import { CloseBtn, IconBtn } from "../../../../styledCpns/Inputs";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Box } from "../../../../styledCpns/Layout";
import { OneBtnBar } from "../../../CommonCpns";
import Setup from "./Setup";
import { colorCodes } from "../../../../data/misc";
import { applySettings } from "./helpers";

const StyledDiv = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  border-radius: 0.625rem;
  background-color: ${colorCodes.cardBg3};
`;

export default function Settings(props) {
  const { char, wpType, stdSetupI, close } = props;
  const {
    setSelfMCs,
    setWpRack,
    setSubWpBCs,
    setArtChest,
    setParties,
    setElmtMCs,
    setSetupNames,
    setStdSetupI,
    setCurSetupI,
    setCmpSetupIs
  } = useContext(FixedCts);
  const [tempSetups, setTempSetups] = useState(
    props.setupNames.map((name, index) => ({
      name,
      index,
      checked: props.cmpSetupIs.includes(index)
    }))
  );
  const [err, setErr] = useState(false);

  const stdSetupIndex = useMemo(() => tempSetups[stdSetupI].index, []);
  const tempStdSetupI = useMemo(
    () => tempSetups.findIndex((ts) => ts.index === stdSetupIndex),
    [tempSetups]
  );

  const charName = char.name;
  const tryApply = useCallback(() => {
    let ok = true;
    const names = [];
    for (let { name } of tempSetups) {
      if (!name.length || name.length > 16 || names.includes(name)) {
        ok = false;
        break;
      } else {
        names.push(name);
      }
    }
    if (ok) {
      applySettings(tempSetups, charName, wpType, {
        setSetupNames,
        setCmpSetupIs,
        setSelfMCs,
        setWpRack,
        setSubWpBCs,
        setArtChest,
        setParties,
        setElmtMCs
      });
      setStdSetupI(tempStdSetupI);
      setCurSetupI(tempStdSetupI);
      close();
    } else {
      setErr(true);
    }
  }, [tempSetups, charName, wpType, tempStdSetupI]);

  return (
    <StyledDiv className="full-w full-h p-3 flex-col">
      <CloseBtn variant="negative" className="glow-on-hover" onClick={close} />
      <Text className="mt-2" variant="h3" align="center" color="orange">
        SETTINGS
      </Text>
      <Box height="33.45rem" className="flex-col hide-sb">
        {tempSetups.map((setup, index) => (
          <Setup
            key={index}
            setup={setup}
            index={index}
            standard={index === tempStdSetupI}
            setTempSetups={setTempSetups}
            numOfSetups={tempSetups.length}
          />
        ))}
        {tempSetups.length < 4 && (
          <IconBtn
            className="mt-3 py-2 full-w glow-on-hover"
            icon="fas fa-plus"
            bgColor="#2269d6"
            color="white"
            borderRadius="1rem"
            onClick={() =>
              setTempSetups((prev) => {
                const existedIs = [1, 2, 3, 4];
                for (let ts of prev) {
                  const parts = ts.name.split(" ");
                  if (parts[0] === "Setup" && !isNaN(parts[1])) {
                    const i = existedIs.indexOf(+parts[1]);
                    if (i !== -1) existedIs.splice(i, 1);
                  }
                }
                const newTSs = [...prev];
                newTSs.push({
                  name: "Setup " + existedIs[0],
                  index: null,
                  checked: false
                });
                return newTSs;
              })
            }
          />
        )}
        {err && (
          <Text className="mt-3" color="lightRed" align="center">
            <i>Please choose a unique name for each Setup.</i>
          </Text>
        )}
      </Box>
      <OneBtnBar
        className="mt-3"
        variant="positive"
        text="Apply"
        handleClick={tryApply}
      />
    </StyledDiv>
  );
}
