import { useContext } from "react";
import { PartiesCt, FixedCts, SetupCt } from "../../index";
import { Text } from "../../../../styledCpns/DataDisplay";
import {
  Section,
  Setter,
  SetterInput,
  SetterSelect,
  NoContent
} from "../CmnCpns";
import characters from "../../../../data/characters";
import { swirlables } from "../../../../data/elements";
import { deepCopy } from "../../../../helpers/misc";
import { processNumInp } from "../../../../helpers/processors";

export default function PartyBuffs({ char }) {
  const parties = useContext(PartiesCt);
  const curSetupI = useContext(SetupCt);
  const content = [];

  parties[curSetupI].forEach((teammate, tmI) => {
    if (teammate.DCs.length) {
      content.push(
        <TeammateDebuffs
          key={tmI}
          teammate={teammate}
          curSetupI={curSetupI}
          tmI={tmI}
          char={char}
        />
      );
    }
  });
  return content.length ? content : <NoContent type="debuffs" />;
}

function TeammateDebuffs(props) {
  const { setParties } = useContext(FixedCts);
  const { teammate, curSetupI, tmI } = props;
  const tmData = characters[teammate.name];
  const subContent = [];

  teammate.DCs.forEach((ctrl, dcIndex) => {
    const debuff = tmData.debuffs[dcIndex];
    const { activated } = ctrl;
    subContent.push(
      <Section
        key={dcIndex}
        checked={activated}
        handleCheck={() => {
          setParties((prev) => {
            const newPts = deepCopy(prev);
            newPts[curSetupI][tmI].DCs[dcIndex].activated = !activated;
            return newPts;
          });
        }}
        heading={debuff.src}
        desc={debuff.desc({
          char: props.char,
          inputs: ctrl.inputs,
          toSelf: false
        })}
        setters={
          <SetterSection
            debuff={debuff}
            ctrl={ctrl}
            setParties={setParties}
            curSetupI={curSetupI}
            tmI={tmI}
            dcIndex={dcIndex}
          />
        }
      />
    );
  });
  return (
    <>
      <Text
        className="pt-2 mb-m1"
        variant="h6"
        color={tmData.vision}
        align="center"
      >
        <strong>{teammate.name.toUpperCase()}</strong>
      </Text>
      {subContent}
    </>
  );
}

function SetterSection(props) {
  const { debuff, ctrl, setParties, curSetupI, tmI, dcIndex } = props;
  if (!ctrl.inputs) {
    return null;
  }
  return ctrl.inputs.map((inp, i) => {
    const type = debuff.inputTypes[i];
    let inputCpn;
    if (type === "absorption") {
      inputCpn = (
        <SetterSelect
          value={inp}
          onChange={(e) =>
            setParties((prev) => {
              const newPts = deepCopy(prev);
              newPts[curSetupI][tmI].DCs[dcIndex].inputs[i] = e.target.value;
              return newPts;
            })
          }
        >
          {swirlables.map((opt) => (
            <option key={opt}>{opt}</option>
          ))}
        </SetterSelect>
      );
    } else {
      inputCpn = (
        <SetterInput
          type="text"
          value={inp}
          onChange={(e) =>
            setParties((prev) => {
              const newPts = deepCopy(prev);
              newPts[curSetupI][tmI].DCs[dcIndex].inputs[i] = processNumInp(
                e.target.value,
                inp,
                13
              );
              return newPts;
            })
          }
        />
      );
    }
    return (
      <Setter key={i}>
        <p className="label">
          {type === "absorption" ? "Element Absorbed" : debuff.src + " level"}
        </p>
        {inputCpn}
      </Setter>
    );
  });
}
