import { useContext } from "react";
import { ArtChestCt, FixedCts, SetupCt } from "../../index";
import { Section, Setter, SetterSelect, NoContent } from "../CmnCpns";
import artifactBuffs from "../../../../data/artifacts/artifactBuffs";
import { crystallizables } from "../../../../data/elements";
import { deepCopy } from "../../../../helpers/misc";

export default function ArtBuffs(props) {
  const artChest = useContext(ArtChestCt);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const { sets, BCs, subBCs } = artChest[curSetupI];
  const content = [];

  const name = sets[0]?.name;
  BCs.forEach(({ activated, index, input }, bcIndex) => {
    const buff = artifactBuffs[name][index];
    content.push(
      <Section
        key={name + bcIndex}
        checked={activated}
        handleCheck={() =>
          setArtChest((prev) => {
            const newArts = deepCopy(prev);
            newArts[curSetupI].BCs[bcIndex].activated = !activated;
            return newArts;
          })
        }
        heading={name + " (self)"}
        desc={buff.desc}
        setters={
          <SetterSection
            buff={buff}
            input={input}
            handleChange={(value) =>
              setArtChest((prev) => {
                const newArts = deepCopy(prev);
                newArts[curSetupI].BCs[bcIndex].input = isNaN(value)
                  ? value
                  : +value;
                return newArts;
              })
            }
          />
        }
      />
    );
  });
  subBCs.forEach((ctrl, bcIndex) => {
    const { set, activated } = ctrl;
    const buff = artifactBuffs[set][ctrl.index];
    content.push(
      <Section
        key={set + bcIndex}
        checked={activated}
        handleCheck={() => {
          setArtChest((prev) => {
            const newArts = deepCopy(prev);
            newArts[curSetupI].subBCs[bcIndex].activated = !activated;
            return newArts;
          });
        }}
        heading={set}
        desc={buff.desc}
        setters={
          Boolean(buff.inputType) && (
            <SetterSection
              buff={buff}
              input={ctrl.input}
              handleChange={(value) =>
                setArtChest((prev) => {
                  const newArts = deepCopy(prev);
                  newArts[curSetupI].subBCs[bcIndex].input = isNaN(value)
                    ? value
                    : +value;
                  return newArts;
                })
              }
            />
          )
        }
      />
    );
  });
  return content.length ? content : <NoContent type="buffs" />;
}

function SetterSection(props) {
  const { inputType, maxStacks } = props.buff;
  if (!inputType) {
    return null;
  }
  const isStacks = inputType === "stacks";
  const options = isStacks ? [...Array(maxStacks).keys()] : crystallizables;
  return (
    <Setter>
      <p className="label">{isStacks ? "Stacks" : "Element"}</p>
      <SetterSelect
        value={props.input}
        onChange={(e) => props.handleChange(e.target.value)}
      >
        {options.map((opt) => (
          <option key={opt}>{isStacks ? opt + 1 : opt}</option>
        ))}
      </SetterSelect>
    </Setter>
  );
}
