import { useContext, useState } from "react";
import { SetDBCt, MyCharsCt } from "../../App";
import { CardCarpet } from "../../styledCpns/Layout";
import { Button } from "../../styledCpns/Inputs";
import { EquippedChar, ConfirmSwap } from "../CommonCpns";
import MyArtCard from "../Readers/MyArtCard";
import Picker from "../Picker";
import characters from "../../data/characters";
import artifacts from "../../data/artifacts";
import { deepCopy } from "../../helpers/misc";

export default function RightSection(props) {
  const { art } = props;
  const { ID, type, user } = art;
  const myChars = useContext(MyCharsCt);
  const { setMyChars, setMyArts } = useContext(SetDBCt);
  const [pickerOn, setPickerOn] = useState(false);
  const [swapTarget, setSwapTarget] = useState(false);

  const swap = (name) => {
    const targetCharI = myChars.findIndex((char) => char.name === name);
    setMyArts((prev) => {
      const newArts = deepCopy(prev);
      const artI_new = newArts.findIndex((item) => item.ID === ID);
      newArts[artI_new].user = name;
      const artID = myChars[targetCharI].artIDs[type];
      if (artID) {
        const artI_old = newArts.findIndex((item) => item.ID === artID);
        newArts[artI_old].user = user;
      }
      return newArts;
    });
    setMyChars((prev) => {
      const newChars = deepCopy(prev);
      if (user) {
        const currentCharI = newChars.findIndex((char) => char.name === user);
        newChars[currentCharI].artIDs[type] =
          newChars[targetCharI].artIDs[type];
      }
      newChars[targetCharI].artIDs[type] = ID;
      return newChars;
    });
  };

  return (
    <div className="flex-col justify-between">
      <CardCarpet>
        <div className="card_content hide-sb">
          {art ? <MyArtCard art={art} mutable={true} /> : null}
        </div>
        {art ? (
          <div className="mt-3 flex justify-center">
            <Button
              className="mr-5"
              variant="negative"
              onClick={() => {
                setMyArts((prev) => {
                  const newArts = deepCopy(prev);
                  const index = newArts.findIndex((item) => item.ID === art.ID);
                  newArts.splice(index, 1);
                  return newArts;
                });
                if (user) {
                  setMyChars((prev) => {
                    const newChars = deepCopy(prev);
                    for (let unit of newChars) {
                      if (unit.name === user) {
                        unit.artIDs[art.type] = null;
                        break;
                      }
                    }
                    return newChars;
                  });
                }
              }}
            >
              Remove
            </Button>
            <Button variant="positive" onClick={() => setPickerOn(true)}>
              Equip
            </Button>
          </div>
        ) : null}
      </CardCarpet>
      {art?.user ? <EquippedChar user={user} /> : null}
      {pickerOn && (
        <CharPicker
          user={user}
          myChars={myChars}
          setSwapTarget={setSwapTarget}
          swap={swap}
          close={() => setPickerOn(false)}
        />
      )}
      {swapTarget ? (
        <ConfirmSwap
          user={user}
          item={artifacts[art.set][art.type].name}
          close={() => setSwapTarget(null)}
          swap={() => swap(swapTarget)}
        />
      ) : null}
    </div>
  );
}

function CharPicker(props) {
  const { user } = props;
  const data = [];
  for (let { name, constellation } of props.myChars) {
    if (name !== user) {
      const { icon, rarity, vision, weapon } = characters[name];
      data.push({ name, icon, rarity, vision, weapon, constellation });
    }
  }
  return (
    <Picker
      data={data}
      dataType="Character"
      close={props.close}
      pick={({ name }) => {
        if (user) {
          props.setSwapTarget(name);
        } else {
          props.swap(name);
        }
      }}
    />
  );
}
