import { useContext } from "react";
import { ElmtMCsCt, SetupCt, FixedCts } from "../../index";
import { Green, LGreen } from "../../../../styledCpns/DataDisplay";
import { Section } from "../CmnCpns";
import { deepCopy } from "../../../../helpers/misc";

export default function ElmtBuffs() {
  const elmtMCs = useContext(ElmtMCsCt);
  const curSetupI = useContext(SetupCt);
  const { setElmtMCs } = useContext(FixedCts);
  const { resonance, superconduct } = elmtMCs[curSetupI];
  const geoRsnI = resonance.findIndex((rsn) => rsn.name === "Enduring Rock");
  let geoRsnOn;
  if (geoRsnI >= 0) {
    geoRsnOn = resonance[geoRsnI].activated;
  }

  return (
    <>
      <Section
        checked={superconduct}
        handleCheck={() =>
          setElmtMCs((prev) => {
            const MCs = deepCopy(prev);
            MCs[curSetupI].superconduct = !superconduct;
            return MCs;
          })
        }
        heading="Superconduct"
        desc={
          <>
            Reduces the <LGreen>Physical RES</LGreen> of enemies by{" "}
            <Green>40%</Green> for 12 seconds.
          </>
        }
      />
      {geoRsnI >= 0 && (
        <Section
          key="rock"
          checked={geoRsnOn}
          handleCheck={() =>
            setElmtMCs((prev) => {
              const MCs = deepCopy(prev);
              MCs[curSetupI].resonance[geoRsnI].activated = !geoRsnOn;
              return MCs;
            })
          }
          heading="Enduring Rock"
          desc={
            <>
              Shielded characters dealing DMG to enemies will decrease their{" "}
              <LGreen>Geo RES</LGreen> by <Green>20%</Green> for 15s.
            </>
          }
        />
      )}
    </>
  );
}
