import { useState, useEffect, createContext } from "react";
import { StyledCalculator } from "./styles";
import CalcChar from "./CalcChar";
import Modifiers from "./Modifiers";
import SetupManager from "./SetupManager";
import DmgResults from "./DmgResults";
import characters from "../../data/characters";
import monsters from "../../data/monsters";
import { elmts, infusePrior } from "../../data/elements";
import { initTarget } from "./CalcChar/helpers";
import getBuffedStats from "../../calculators/buffedStats";
import getDmg from "../../calculators/damage";
import { deepCopy } from "../../helpers/misc";

export const FixedCts = createContext();
export const SelfMCsCt = createContext();
export const WpRackCt = createContext();
export const SubWpBCsCt = createContext();
export const ArtChestCt = createContext();
export const PartiesCt = createContext();
export const ElmtMCsCt = createContext();
export const TargetCt = createContext();
export const ResultCt = createContext();
export const SetupCt = createContext();
export const DmgResultsCt = createContext();

export default function Calculators(props) {
  const { char, setChar } = props;
  const [selfMCs, setSelfMCs] = useState(null);
  const [wpRack, setWpRack] = useState(null);
  const [subWpBCs, setSubWpBCs] = useState(null);
  const [artChest, setArtChest] = useState(null);
  const [parties, setParties] = useState(null);
  const [elmtMCs, setElmtMCs] = useState(null);
  const [target, setTarget] = useState(initTarget());
  const [setupNames, setSetupNames] = useState([]);
  const [stdSetupI, setStdSetupI] = useState(0);
  const [curSetupI, setCurSetupI] = useState(0);
  const [cmpSetupIs, setCmpSetupIs] = useState([0]);

  const [monster, setMonster] = useState({
    name: monsters[0].name,
    inputs: []
  });
  useEffect(() => {
    setTarget((prev) => {
      const newTg = deepCopy(prev);
      const { res, addResBn } = monsters[
        monsters.findIndex(({ name }) => name === monster.name)
      ];
      for (let type in res) {
        if (type === "Elemental RES") {
          for (let elmt of elmts) {
            newTg[elmt + " RES"] = res[type];
          }
        } else {
          newTg[type] = res[type];
        }
      }
      if (addResBn) {
        addResBn({ target: newTg, inputs: monster.inputs });
      }
      return newTg;
    });
  }, [monster]);

  let charData;
  const allATTRs = [];
  const allArtSBnes = [];
  const allRxnBnes = [];
  const allInfusion = [];
  const allDmg = [];
  if (char) {
    charData = characters[char.name];
    for (let i = 0; i < setupNames.length; i++) {
      const finalInfusion = getFinalInfusion(
        selfMCs[i],
        charData.vision,
        parties[i]
      );
      allInfusion.push(finalInfusion);
      const { ATTRs, artSBnes, rxnBnes, bonuses } = getBuffedStats(
        charData,
        char,
        selfMCs[i].BCs,
        wpRack[i],
        subWpBCs[i],
        artChest[i],
        elmtMCs[i].resonance,
        parties[i],
        finalInfusion
      );
      allATTRs.push(ATTRs);
      allArtSBnes.push(artSBnes);
      allRxnBnes.push(rxnBnes);
      allDmg.push(
        getDmg(
          deepCopy(char),
          selfMCs[i],
          parties[i],
          wpRack[i],
          artChest[i].subDCs,
          ATTRs,
          bonuses,
          rxnBnes,
          finalInfusion,
          elmtMCs[i],
          target
        )
      );
    }
  }
  return (
    <FixedCts.Provider
      value={{
        setSelfMCs,
        setWpRack,
        setSubWpBCs,
        setArtChest,
        setParties,
        setElmtMCs,
        setTarget,
        setMonster,
        setSetupNames,
        setStdSetupI,
        setCurSetupI,
        setCmpSetupIs
      }}
    >
      <SelfMCsCt.Provider value={selfMCs}>
        <WpRackCt.Provider value={wpRack}>
          <SubWpBCsCt.Provider value={subWpBCs}>
            <ArtChestCt.Provider value={artChest}>
              <PartiesCt.Provider value={parties}>
                <ElmtMCsCt.Provider value={elmtMCs}>
                  <TargetCt.Provider value={target}>
                    <ResultCt.Provider
                      value={{ allATTRs, allArtSBnes, allRxnBnes, allInfusion }}
                    >
                      <SetupCt.Provider value={curSetupI}>
                        <StyledCalculator>
                          <div className="flex">
                            <CalcChar
                              char={char}
                              charData={charData}
                              setChar={setChar}
                            />
                            <Modifiers char={char} monster={monster} />
                            <SetupManager
                              char={char}
                              charData={charData}
                              setupNames={setupNames}
                              stdSetupI={stdSetupI}
                              setStdSetupI={setStdSetupI}
                              cmpSetupIs={cmpSetupIs}
                              setCmpSetupIs={setCmpSetupIs}
                            />
                            <DmgResultsCt.Provider
                              value={{
                                setupNames,
                                stdSetupI,
                                cmpSetupIs,
                                allDmg
                              }}
                            >
                              <DmgResults char={char} charData={charData} />
                            </DmgResultsCt.Provider>
                          </div>
                        </StyledCalculator>
                      </SetupCt.Provider>
                    </ResultCt.Provider>
                  </TargetCt.Provider>
                </ElmtMCsCt.Provider>
              </PartiesCt.Provider>
            </ArtChestCt.Provider>
          </SubWpBCsCt.Provider>
        </WpRackCt.Provider>
      </SelfMCsCt.Provider>
    </FixedCts.Provider>
  );
}

function getFinalInfusion(MCs, ownVision, party) {
  const result = {};
  for (let type of ["NA", "CA", "PA"]) {
    result[type] = strongestInfusion(type, MCs.infusion, ownVision, party);
  }
  return result;
}

function strongestInfusion(type, infusion, ownVision, party) {
  const contenders = ["Physical"];
  for (let ifs of infusion) {
    if (ifs.range.includes(type)) {
      if (!ifs.canBeOverrided) {
        return ownVision;
      } else if (!contenders.includes(ownVision)) {
        contenders.unshift(ownVision);
      }
    }
  }
  for (let teammate of party) {
    if (teammate.infusion) {
      for (let i in contenders) {
        if (
          infusePrior.indexOf(teammate.infusion.elmt) <
          infusePrior.indexOf(contenders[i])
        ) {
          contenders.splice(i, 0, teammate.infusion.elmt);
          break;
        }
      }
    }
  }
  return contenders[0];
}
