import { Img } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { IconBtn } from "../../../styledCpns/Inputs";
import characters from "../../../data/characters";
import { wikia } from "../../../data/misc";

export function TeammateIcon(props) {
  return (
    <>
      <Box
        className="zoomin-on-hover overflow-hidden"
        borderRadius="50%"
        bgColor="cardBg3"
      >
        <Img
          className="pointer"
          src={wikia + characters[props.teammate.name].icon + ".png"}
          alt="teammate"
          width="100%"
          borderRadius="inherit"
          onClick={props.handleClick}
        />
      </Box>
      <IconBtn
        icon="fas fa-times"
        position="absolute"
        bottom="-0.25rem"
        right="-0.625rem"
        variant="negative"
        size="1.75rem"
        fontSize="0.875rem"
        className="glow-on-hover"
        onClick={props.removeTeammate}
      />
    </>
  );
}

export function TeammateSlot(props) {
  return (
    <IconBtn
      icon="fas fa-plus"
      color="rgb(255, 255, 255, 0.7)"
      bgColor="cardBg3"
      size="100%"
      fontSize="1.5rem"
      className="glow-on-hover"
      onClick={props.handleClick}
    />
  );
}
