import styled from "styled-components";
import { Box, ModalCarpet } from "../styledCpns/Layout";
import { Text, ColoredSpan, List } from "../styledCpns/DataDisplay";
import { Button } from "../styledCpns/Inputs";
import artifacts from "../data/artifacts";
import { colorCodes } from "../data/misc";
import { useCloseModal } from "../helpers/hooks";

export function EquippedChar({ user }) {
  return (
    <Box className="px-4 py-1" bgColor="#ffe7bb">
      <Text fontWeight={700} color="black">
        Equipped: {user}
      </Text>
    </Box>
  );
}

export function OneBtnBar(props) {
  const { className, variant } = props;
  return (
    <div className={"flex justify-center" + (className ? " " + className : "")}>
      <Button
        className="glow-on-hover"
        variant={variant}
        onClick={props.handleClick}
      >
        {props.text}
      </Button>
    </div>
  );
}

export function SetBonus({ sets }) {
  return (
    <div>
      <Text variant="h6" color="orange" fontWeight={700}>
        Set Bonus
      </Text>
      {sets.length > 0 ? (
        sets.map((set, i) => {
          const content = [];
          for (let i = 0; i <= set.bonusLv; i++) {
            content.push(
              <li key={i} className="mt-1">
                <ColoredSpan color="orange">
                  {artifacts[set.name].setBnes[i].type}:
                </ColoredSpan>{" "}
                <ColoredSpan color="green">
                  {artifacts[set.name].setBnes[i].desc}
                </ColoredSpan>
              </li>
            );
          }
          return (
            <div key={i} className="mt-1">
              <Text variant="h6" fontWeight={700} color="green">
                {set.name}
              </Text>
              <List className="pl-4">{content}</List>
            </div>
          );
        })
      ) : (
        <Text color="rgb(185, 185, 185)" fontWeight={700}>
          No Set Bonus
        </Text>
      )}
    </div>
  );
}

const StyledSvg = styled.svg`
  width: 1.25rem;
  height: 1.25rem;
  fill: ${(props) => colorCodes[props.rarity]};
`;

export const Star = (props) => (
  <StyledSvg viewBox="0 0 24 24" rarity={props.rarity}>
    <path d="M12,17.27L18.18,21L16.54,13.97L22,9.24L14.81,8.62L12,2L9.19,8.62L2,9.24L7.45,13.97L5.82,21L12,17.27Z" />
  </StyledSvg>
);

export const StarLine = ({ rarity, className = "" }) => {
  const result = [];
  for (let i = 0; i < (rarity === "Gold" ? 5 : 4); i++) {
    result.push(<Star key={i} rarity={rarity} />);
  }
  return <div className={"flex align-center " + className}>{result}</div>;
};

export const TalentXtraLv = ({ xtraLv }) =>
  xtraLv ? (
    <Text className="ml-2" fontWeight={700} color="green">
      +{xtraLv}
    </Text>
  ) : null;

export function ConfirmSwap(props) {
  const { close } = props;
  const ref = useCloseModal(close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="p-3 relative"
        width="22.5rem"
        borderRadius="1rem"
        bgColor="cardBg3"
      >
        <div className="py-3">
          <Text variant="h4" align="center">
            {props.user} is currently using {props.item}. Swap?
          </Text>
        </div>
        <div className="mt-3 flex justify-center">
          <Button
            className="mr-5 glow-on-hover"
            variant="negative"
            onClick={close}
          >
            Cancel
          </Button>
          <Button
            className="glow-on-hover"
            variant="positive"
            onClick={() => {
              props.swap();
              close();
            }}
          >
            Confirm
          </Button>
        </div>
      </Box>
    </ModalCarpet>
  );
}
