import { useState } from "react";
import ElmtBuffs from "./ElmtBuffs";
import SelfBuffs from "./SelfBuffs";
import PartyBuffs from "./PartyBuffs";
import WpBuffs from "./WpBuffs";
import ArtBuffs from "./ArtBuffs";
import { CollapseHeading } from "../../../../styledCpns/DataDisplay";
import { StyledCollapse } from "../CmnCpns";

export default function Buffs({ char }) {
  const [expanded, setExpanded] = useState([]);
  return (
    <div>
      {["Resonance & Reactions", "Self", "Party", "Weapons", "Artifacts"].map(
        (cate, i) => (
          <div key={i} className={expanded[i] ? "mb-4" : ""}>
            <CollapseHeading
              className={"mb-2p5" + (expanded[i] ? " active" : "")}
              onClick={() =>
                setExpanded((prev) => {
                  const newEpd = [...prev];
                  newEpd[i] = !newEpd[i];
                  return newEpd;
                })
              }
            >
              {cate}
            </CollapseHeading>
            <StyledCollapse in={Boolean(expanded[i])}>
              {
                {
                  "Resonance & Reactions": <ElmtBuffs char={char} />,
                  Self: <SelfBuffs char={char} />,
                  Party: <PartyBuffs char={char} />,
                  Weapons: <WpBuffs />,
                  Artifacts: <ArtBuffs />
                }[cate]
              }
            </StyledCollapse>
          </div>
        )
      )}
    </div>
  );
}
