import React, { useState, useEffect, useContext } from "react";
import { DmgResultsCt } from "../index";
import { Collapse } from "@material-ui/core";
import { Text } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Select } from "../../../styledCpns/Inputs";
import { Table } from "./styles";
import OneSetup from "./OneSetup";
import MultiSetup from "./MultiSetup";

export default function Inner(props) {
  const { char, namesForTables } = props;
  const { setupNames, stdSetupI, cmpSetupIs, allDmg } = useContext(
    DmgResultsCt
  );
  const [focus, setFocus] = useState("Average");
  const [closed, setClosed] = useState([]);
  const charName = char ? char.name : "";
  useEffect(() => {
    setClosed([]);
  }, [charName]);

  return (
    <>
      {cmpSetupIs.length > 1 ? (
        <div className="mb-3 flex justify-center">
          <Text className="mr-2">Choose a focus</Text>
          <Select
            fontSize="1rem"
            align="center"
            color="lightGold"
            value={focus}
            onChange={(e) => setFocus(e.target.value)}
          >
            {["Non-crit", "Crit", "Average"].map((opt) => (
              <option key={opt}>{opt}</option>
            ))}
          </Select>
        </div>
      ) : (
        <Text className="mx-3 my-2" fontWeight={700} align="center">
          {setupNames[stdSetupI].toUpperCase()}
        </Text>
      )}
      <Box height="36.05rem" className="hide-sb">
        {namesForTables.map((name, i) => {
          const stdValues = allDmg[stdSetupI][name.main];
          return (
            <div
              key={i}
              className={
                "flex-col" + (i === namesForTables.length - 1 ? "" : " mb-3")
              }
            >
              <Box
                className="mx-auto px-3 mb-2 flex align-center cursor-default"
                borderRadius="1rem"
                bgColor="orange"
                onClick={() =>
                  setClosed((prev) => {
                    const newC = [...prev];
                    newC[i] = !newC[i];
                    return newC;
                  })
                }
              >
                <Text variant="h5" fontWeight={700} color="black">
                  {name.main}
                </Text>
                <Text className="ml-2" variant="subtitle1" color="black">
                  <i
                    className={"fas fa-chevron-" + (closed[i] ? "down" : "up")}
                  />
                </Text>
              </Box>
              <Collapse in={!closed[i]}>
                <div className="custom-sb">
                  <Table>
                    {cmpSetupIs.length > 1 ? (
                      <MultiSetup
                        name={name}
                        stdValues={stdValues}
                        focus={focus}
                      />
                    ) : (
                      <OneSetup subNames={name.subs} stdValues={stdValues} />
                    )}
                  </Table>
                </div>
              </Collapse>
            </div>
          );
        })}
      </Box>
    </>
  );
}
