import { useState } from "react";
import ElmtDebuffs from "./ElmtDebuffs";
import SelfDebuffs from "./SelfDebuffs";
import PartyDebuffs from "./PartyDebuffs";
import ArtDebuffs from "./ArtDebuffs";
import { CollapseHeading } from "../../../../styledCpns/DataDisplay";
import { StyledCollapse } from "../CmnCpns";

export default function Buffs({ char }) {
  const [expanding, setExpanding] = useState([]);
  return (
    <div>
      {["Resonance & Reactions", "Self", "Party", "Artifacts"].map(
        (cate, i) => (
          <div key={i} className={expanding[i] ? "mb-4" : ""}>
            <CollapseHeading
              className={"mb-2p5" + (expanding[i] ? " active" : "")}
              onClick={() =>
                setExpanding((prev) => {
                  const newEpd = [...prev];
                  newEpd[i] = !newEpd[i];
                  return newEpd;
                })
              }
            >
              {cate}
            </CollapseHeading>
            <StyledCollapse in={Boolean(expanding[i])}>
              {
                {
                  "Resonance & Reactions": <ElmtDebuffs char={char} />,
                  Self: <SelfDebuffs char={char} />,
                  Party: <PartyDebuffs char={char} />,
                  Artifacts: <ArtDebuffs />
                }[cate]
              }
            </StyledCollapse>
          </div>
        )
      )}
    </div>
  );
}
