import {
  initSelfMCs,
  getMainWpBCs,
  getSubArtBCs,
  getSubArtDCs,
  initParty,
  initElmtMCs
} from "../../CalcChar/helpers";
import { initWeapon } from "../../../Picker/WpPicker";

function getResult(indexes, prev, newElmt) {
  const result = [];
  for (let index of indexes) {
    if (index === null) result.push(newElmt);
    else result.push(prev[index]);
  }
  return result;
}

export function applySettings(tempSetups, charName, wpType, CBs) {
  const setupNames = [];
  const cmpSetupIs = [];
  const indexes = [];
  tempSetups.forEach((setup, i) => {
    setupNames.push(setup.name);
    if (setup.checked) {
      cmpSetupIs.push(i);
    }
    indexes.push(setup.index);
  });
  CBs.setSetupNames(setupNames);
  CBs.setCmpSetupIs(cmpSetupIs);
  CBs.setSelfMCs((prev) => getResult(indexes, prev, initSelfMCs(charName)));
  CBs.setWpRack((prev) => {
    const result = [];
    let ID = Date.now();
    for (let index of indexes) {
      if (index === null) {
        const newWp = initWeapon({ ID: ID++, type: wpType });
        result.push({ ...newWp, BCs: getMainWpBCs(wpType, newWp.name) });
      } else {
        result.push(prev[index]);
      }
    }
    return result;
  });
  CBs.setSubWpBCs((prev) => getResult(indexes, prev, {}));
  CBs.setArtChest((prev) =>
    getResult(indexes, prev, {
      pieces: {
        flower: null,
        plume: null,
        sands: null,
        goblet: null,
        circlet: null
      },
      sets: [],
      BCs: [],
      subBCs: getSubArtBCs(null),
      subDCs: getSubArtDCs()
    })
  );
  CBs.setParties((prev) => getResult(indexes, prev, initParty()));
  CBs.setElmtMCs((prev) => getResult(indexes, prev, initElmtMCs()));
}
