import styled from "styled-components";
import { useState } from "react";
import { Box, ModalCarpet } from "../../../styledCpns/Layout";
import { IconBtn } from "../../../styledCpns/Inputs";
import { Card } from "../styles";
import Inner from "./Inner";
import { tfmRxns } from "../../../data/elements";
import { useCloseModal } from "../../../helpers/hooks";

const EnlargeBtn = styled(IconBtn)`
  position: absolute;
  top: 1rem;
  left: 1rem;
`;

export default function DmgResults(props) {
  const { char } = props;
  const [enlarged, setEnlarged] = useState(false);
  if (!char) {
    return (
      <Card className="px-3 pt-2 pb-4" bgColor="cardBg3">
        <Box height="38.5rem" />
      </Card>
    );
  }
  const namesForTables = getNamesForTables(props.charData);
  return (
    <Card className="px-3 pt-2 pb-4 relative" bgColor="cardBg3">
      {window.innerWidth >= 1050 && (
        <EnlargeBtn
          className="glow-on-hover"
          icon="fas fa-expand-arrows-alt"
          size="1.5rem"
          fontSize="1rem"
          variant="positive"
          onClick={() => setEnlarged(true)}
        />
      )}
      <Inner char={char} namesForTables={namesForTables} />
      {enlarged && (
        <EnlargedInner
          char={char}
          namesForTables={namesForTables}
          close={() => setEnlarged(false)}
        />
      )}
    </Card>
  );
}

function EnlargedInner(props) {
  const ref = useCloseModal(props.close);
  return (
    <ModalCarpet>
      <Box
        ref={ref}
        className="px-4 py-3"
        borderRadius="0.5rem"
        bgColor="cardBg3"
      >
        <Inner char={props.char} namesForTables={props.namesForTables} />
      </Box>
    </ModalCarpet>
  );
}

function getNamesForTables(charData) {
  const result = charData.actvTalents.map((talent) => ({
    main: talent.type,
    subs: talent.stats.map(({ name }) => name)
  }));
  result.push({
    main: "Reactions DMG",
    subs: Object.keys(tfmRxns)
  });
  // if (cmpSetupIs.length === 1) {
  //   const { pasvProcD } = weapons[weapon.type][weapon.name];
  //   if (pasvProcD) {
  //     const node = { name: "Weapon Attack Proc", subNames: [] };
  //     pasvProcD.forEach((dmg) => {
  //       node.subNames.push(dmg.name);
  //     });
  //     result.push(node);
  //   }
  // }
  return result;
}
