import { useState, useContext } from "react";
import { ArtChestCt, SetupCt, FixedCts } from "../index";
import { Img } from "../../../styledCpns/DataDisplay";
import { Box } from "../../../styledCpns/Layout";
import { Pedestal } from "./styles";
import ArtPicker from "../../Picker/ArtPicker";
import { FilledCard, EmptyCard } from "./ArtifactCard";
import artifacts from "../../../data/artifacts";
import artifactBuffs from "../../../data/artifacts/artifactBuffs";
import { wikia, artifactIcons } from "../../../data/misc";
import { getMainArtBCs } from "../CalcChar/helpers";
import { deepCopy } from "../../../helpers/misc";
import { processNumInp, getArtSets } from "../../../helpers/processors";

export default function SectionArts() {
  const artChest = useContext(ArtChestCt);
  const curSetupI = useContext(SetupCt);
  const { setArtChest } = useContext(FixedCts);
  const [tab, setTab] = useState(null);
  const { pieces } = artChest[curSetupI];
  const [pickerOn, setPickerOn] = useState(false);

  return (
    <Pedestal>
      <div className="flex">
        {Object.keys(artifactIcons).map((type, i) => (
          <Box
            key={i}
            border={
              type === tab ? "2px solid white" : "0.5px solid transparent"
            }
          >
            <Box
              className="pointer"
              bgImg={pieces[type] ? "Gold" : "Gray"}
              onClick={() => setTab(type === tab ? null : type)}
            >
              <Img
                width="100%"
                src={
                  wikia +
                  (pieces[type]
                    ? artifacts[pieces[type].set][type].icon
                    : artifactIcons[type]) +
                  ".png"
                }
                alt={type}
              />
            </Box>
          </Box>
        ))}
      </div>
      {Boolean(tab) ? (
        pieces[tab] ? (
          <FilledCard
            art={pieces[tab]}
            switch={() => setPickerOn(true)}
            remove={() => setArtPiece(curSetupI, tab, null, setArtChest)}
            enhance={(e) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tab].level = +e.target.value.substr(1);
                return chest;
              })
            }
            changeMSType={(e) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tab].mainSType = e.target.value;
                return chest;
              })
            }
            changeSSType={(value, ssIndex) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tab].subS[ssIndex].type = value;
                return chest;
              })
            }
            changeSSVal={(value, ssIndex, oldValue) =>
              setArtChest((prev) => {
                const chest = deepCopy(prev);
                chest[curSetupI].pieces[tab].subS[ssIndex].val = processNumInp(
                  value,
                  oldValue,
                  9999
                );
                return chest;
              })
            }
          />
        ) : (
          <EmptyCard type={tab} switch={() => setPickerOn(true)} />
        )
      ) : null}
      {pickerOn && (
        <ArtPicker
          artType={tab}
          close={() => setPickerOn(false)}
          pick={(item) => setArtPiece(curSetupI, tab, item, setArtChest)}
        />
      )}
    </Pedestal>
  );
}

function setArtPiece(curSetupI, artType, newPiece, setArtChest) {
  setArtChest((prev) => {
    const chest = deepCopy(prev);
    const art = chest[curSetupI];
    let oldBonusLv = art.sets[0]?.bonusLv;

    art.pieces[artType] = newPiece;
    art.sets = getArtSets(art.pieces);
    const set = art.sets[0];
    if (set) {
      if (oldBonusLv === 0 && set.bonusLv) {
        art.BCs = getMainArtBCs(set.name);
        let pos = art.subBCs.findIndex((BC) => BC.set === set.name);
        if (pos !== -1) {
          art.subBCs.splice(pos, 1);
        }
      } else if (oldBonusLv && !set.bonusLv && artifactBuffs[set.name]) {
        art.BCs = [];
        for (let index in artifactBuffs[set.name]) {
          const buff = artifactBuffs[set.name][index];
          if (buff.affect !== "self") {
            const node = { set, activated: false, index };
            if (buff.inputType === "crystallize") {
              node.input = "Pyro";
            }
            art.subBCs.push(node);
          }
        }
      }
    }
    return chest;
  });
}
