import { useState, useContext, useRef } from "react";
import { SetDBCt } from "../../App";
import { ModalCarpet } from "../../styledCpns/Layout";
import { Button } from "../../styledCpns/Inputs";
import { Text } from "../../styledCpns/DataDisplay";
import { LoadBtn, StyledInner, CloseBtn, Divider } from "./styles";

export default function Upload() {
  const [optionsOn, setOptionsOn] = useState(false);
  const [err, setErr] = useState(false);
  const { setMyChars, setMyWps, setMyArts } = useContext(SetDBCt);

  const loadFromLocalStorage = () => {
    const data = localStorage.getItem("GDC_Data");
    if (data) {
      setMyChars(data.Characters);
      setMyWps(data.Weapons);
      setMyArts(data.Artifacts);
      setOptionsOn(false);
    } else {
      setErr(true);
    }
  };

  const chooseRef = useRef();
  const upload = () => {
    const file = chooseRef.current.files[0];
    const reader = new FileReader();
    if (file?.type.match(/text.*/)) {
      reader.onload = function (event) {
        const data = JSON.parse(event.target.result);
        setMyChars(data.Characters);
        setMyWps(data.Weapons);
        setMyArts(data.Artifacts);
        setOptionsOn(false);
      };
      reader.readAsText(file);
    }
  };

  return (
    <div>
      <LoadBtn className="pointer" onClick={() => setOptionsOn(true)}>
        <i className="fas fa-upload mr-2" />
        <Text fontWeight={700}>Upload</Text>
      </LoadBtn>
      {optionsOn && (
        <ModalCarpet>
          <StyledInner className="pt-2 pb-3">
            <CloseBtn
              className="ml-auto mr-2 mb-2"
              onClick={() => {
                setOptionsOn(false);
                setErr(false);
              }}
            />
            <div className="option flex-col align-center">
              <Text variant="h6" fontWeight={700}>
                Load from Local Storage
              </Text>
              {err && (
                <Text color="red">
                  ! There's no data in your Local Storage !
                </Text>
              )}
              <Button
                className="my-1"
                variant="positive"
                onClick={loadFromLocalStorage}
              >
                Load
              </Button>
            </div>
            <Divider />
            <div className="option flex-col align-center">
              <Text variant="h6" fontWeight={700}>
                Upload a .TXT file
              </Text>
              <input
                ref={chooseRef}
                style={{ display: "none" }}
                type="file"
                accept="text/*"
                onChange={upload}
              />
              <Button
                className="my-1"
                variant="positive"
                onClick={() => {
                  chooseRef.current.click();
                }}
              >
                Choose File
              </Button>
            </div>
          </StyledInner>
        </ModalCarpet>
      )}
    </div>
  );
}
