import styled from "styled-components";
import { IconBtn } from "../../../../styledCpns/Inputs";
import { Text } from "../../../../styledCpns/DataDisplay";
import { Box } from "../../../../styledCpns/Layout";
import { deepCopy } from "../../../../helpers/misc";

const TextInput = styled.input`
  padding: 0.5rem 1rem 0.25rem;
  border-radius: 0.375rem;
  font-size: 1rem;
  text-align: center;
  outline: none;
  &:focus {
    box-shadow: 0 0 0 2px orange;
  }
`;

const CheckBox = styled.input`
  position: absolute;
  right: 0.5rem;
  transform: scale(2);
`;

const ActionBtn = ({ icon, ...others }) => (
  <IconBtn icon={"fas fa-" + icon} size="2rem" fontSize="1rem" {...others} />
);

export default function Setup(props) {
  const { setup, index, standard, setTempSetups } = props;
  const isNew = setup.index === null;
  return (
    <Box className="mt-3 p-2" borderRadius="0.5rem" bgColor="cardBg1">
      <TextInput
        type="text"
        className="full-w"
        value={setup.name}
        onChange={(e) => {
          if (e.target.value.length <= 16) {
            setTempSetups((prev) => {
              const newTSs = deepCopy(prev);
              newTSs[index].name = e.target.value;
              return newTSs;
            });
          }
        }}
      />
      <div className="mt-3 flex justify-between">
        <div className="ml-1 flex">
          <ActionBtn
            icon="trash-alt"
            className={
              "p-2 mr-4 " + (standard ? "unavailable" : "glow-on-hover")
            }
            color="white"
            bgColor="red"
            onClick={() => {
              if (!standard) {
                setTempSetups((prev) => {
                  const newTSs = [...prev];
                  newTSs.splice(index, 1);
                  return newTSs;
                });
              }
            }}
          />
          <ActionBtn
            icon="copy"
            className={"mr-4 " + (isNew ? "unavailable" : "glow-on-hover")}
            variant="positive"
            onClick={() => {
              if (!isNew && props.numOfSetups < 4) {
                setTempSetups((prev) => {
                  const newTSs = [...prev];
                  newTSs.push({
                    name: setup.name + " (copy)",
                    index: setup.index,
                    checked: false
                  });
                  return newTSs;
                });
              }
            }}
          />
          {/* <ActionBtn
            icon="save"
            className={"mr-4 " + (isNew ? "unavailable" : "glow-on-hover")}
            color="black"
            bgColor="Green"
          /> */}
        </div>
        <div className="flex align-center relative">
          <Text className="mr-5 pr-2">Compared</Text>
          <CheckBox
            type="checkbox"
            className={standard ? "unavailable" : ""}
            checked={setup.checked}
            onChange={() => {
              if (!standard) {
                setTempSetups((prev) => {
                  const newTSs = deepCopy(prev);
                  newTSs[index].checked = !prev[index].checked;
                  return newTSs;
                });
              }
            }}
          />
        </div>
      </div>
    </Box>
  );
}
