export function getArtSets(arts = {}) {
  const sets = [];
  const count = {};
  for (let key in arts) {
    if (arts[key]) {
      const name = arts[key].set;
      count[name] = (count[name] || 0) + 1;
      if (count[name] === 2) {
        sets.push({ name, bonusLv: 0 });
      } else if (count[name] === 4) {
        sets[0].bonusLv = 1;
      }
    }
  }
  return sets;
}

export function processNumInp(inp, before, max) {
  if (inp === "") return 0;
  if (!isNaN(inp) && inp <= max) {
    if (inp.slice(-1) === ".") return inp;
    return Math.round(inp * 10) / 10;
  }
  return before;
}
