import styled from "styled-components";
import { Text } from "../styledCpns/DataDisplay";
import { Box } from "../styledCpns/Layout";
import { LoadBtn } from "./LoadControl/styles";
import Download from "./LoadControl/Download";
import Upload from "./LoadControl/Upload";
import { colorCodes } from "../data/misc";

export default function NavBar(props) {
  return (
    <Box bgColor="rgba(0, 0, 0, 0.6)">
      {window.innerWidth >= 1050 && <PcInner {...props} />}
    </Box>
  );
}

const Tab = styled.div`
  background-color: ${colorCodes.cardBg3};
  &.current {
    background-color: ${colorCodes.cardBg1};
  }
  &:hover {
    color: ${colorCodes.orange};
  }
`;

function PcInner(props) {
  return (
    <div className="flex justify-between">
      <div className="flex">
        {["Characters", "Weapons", "Artifacts", "Calculator"].map((item, i) => (
          <Tab
            key={i}
            className={"px-2 pt-1" + (item === props.current ? " current" : "")}
            onClick={() => props.setTab(item)}
          >
            <Text className="pointer" fontWeight={700}>
              {i < 3 && "My"} {item}
            </Text>
          </Tab>
        ))}
      </div>
      <Box className="px-1 flex" bgColor="cardBg3">
        <LoadBtn className="pointer" onClick={() => props.setIntroOn(true)}>
          <i className="fas fa-info-circle mr-2" />
          <Text fontWeight={700}>Guides and Credit</Text>
        </LoadBtn>
        <Download />
        <Upload />
      </Box>
    </div>
  );
}
