import "./styles.css";
import "./custom.css";
import styled from "styled-components";
import { useState, createContext } from "react";
import NavBar from "./components/NavBar";
import Intro from "./components/Intro";
import MyCharacters from "./components/MyCharacters";
import MyWeapons from "./components/MyWeapons";
import MyArtifacts from "./components/MyArtifacts";
import Calculator from "./components/Calculator";

export const SetDBCt = createContext();
export const MyCharsCt = createContext();
export const MyWpsCt = createContext();
export const MyArtsCt = createContext();

export default function App() {
  const [tab, setTab] = useState("Calculator");
  const [introOn, setIntroOn] = useState(true);
  const [myChars, setMyChars] = useState([]);
  const [myWeapons, setMyWps] = useState([]);
  const [myArts, setMyArts] = useState([]);
  const [caclChar, setCalcChar] = useState(null);

  return (
    <div className="App flex-col">
      <SetDBCt.Provider value={{ setMyChars, setMyWps, setMyArts }}>
        <MyCharsCt.Provider value={myChars}>
          <MyWpsCt.Provider value={myWeapons}>
            <MyArtsCt.Provider value={myArts}>
              <NavBar current={tab} setTab={setTab} setIntroOn={setIntroOn} />
              <div className="grow-1 flex-center relative">
                <Calculator char={caclChar} setChar={setCalcChar} />
                {tab !== "Calculator" && (
                  <StyledInventory>
                    {
                      {
                        Characters: myChars ? <MyCharacters /> : null,
                        Weapons: myWeapons ? <MyWeapons /> : null,
                        Artifacts: myArts ? <MyArtifacts /> : null
                      }[tab]
                    }
                  </StyledInventory>
                )}
                {introOn && <Intro close={() => setIntroOn(false)} />}
              </div>
            </MyArtsCt.Provider>
          </MyWpsCt.Provider>
        </MyCharsCt.Provider>
      </SetDBCt.Provider>
    </div>
  );
}

let StyledInventory = styled.div`
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 100%;
`;
